lign: center\n\n    a\n        color: white","\n@keyframes todo\n    0%\n        opacity: 0\n        scale: 0\n        \n    100%\n        opacity: 1\n        scale: 1\n\nmain\n    background-color: #161616\n    border-radius: 1rem\n    width: 35rem\n    height: 80vh\n    position: relative\n    margin-left: 1rem\n\n    div.list\n        height: 90%\n        width: 100%\n        padding: 1rem 0.5rem\n\n        display: flex\n        flex-direction: column\n\n        overflow-y: auto\n\n        div.todo\n            border-radius: 0.5rem\n            opacity: 0\n            padding: 0.5rem 0.25rem\n\n            display: grid\n            grid-template-columns: 1.5rem 1fr\n            align-items: center\n            gap: 1rem\n\n            animation-name: todo\n            animation-duration: 500ms\n            animation-timing-function: ease-in-out\n            animation-delay: 0ms\n            animation-iteration-count: 1\n            animation-direction: normal\n            animation-fill-mode: forwards\n            animation-play-state: running\n\n            &:hover\n                cursor: pointer\n                background-color: #424242\n\n            button.status-checker\n                box-sizing: border-box\n                width: 1.5rem\n                height: 1.5rem\n                border: 0\n                background: url('../assets/images/radio.svg') no-repeat center\n                outline: none\n                &[data-status=\"done\"]\n                    background: url('../assets/images/done.svg') no-repeat center\n                \n                &:hover\n                    cursor: pointer\n                    transform: scale(1.1)\n\n                &:focus\n                    transform: scale(0.95)\n\n            button.description\n                color: white\n                background: none\n                border: 0\n                font-size: 1rem\n                text-align: left\n                transition: color 0.9s linear\n\n                &::before\n                    content: ''\n                    position: absolute\n                    bottom: 1px\n                    background-image: linear-gradient(transparent 0 48%, #757575 50% calc(50% + 1.5px), transparent calc(50% + 1.5px) 100%)\n                    width: 0\n                    height: 100%\n                    transition: width 0.9s linear\n                    display: inline-block\n\n                &[data-status=\"done\"]\n                    color: #8e8e92\n                    &::before\n                        width: 85%\n                &:hover\n                    cursor: pointer\n                    font-weight: normal\n                &:active\n                    color: #0083ff\n\n    div.input\n        width: 100%\n        height: 10%\n        display: flex\n        justify-content: center\n        align-items: center\n        border-bottom-left-radius: 1rem\n        border-bottom-right-radius: 1rem\n        background-color: #252525\n\n        input\n            height: 2.5rem\n            width: 95%\n            background-color: #424242\n            border: 0\n            outline: 0.1rem solid #8e8e92\n            border-radius: 0.5rem\n            color: white\n            padding-left: 1rem\n            &::placeholder\n                font-size: 1rem\n                color: #8e8e92\n            &:focus\n                outline-color: #0083ff\n","\n@keyframes newproject\n    0%\n        background: none\n    100%\n        background: #303030\n\n@keyframes creating-project\n    0%\n        scale: 0\n        height: 0\n    100%\n        scale: 1\n        height: auto\n\naside\n    align-self: stretch\n    background-color: #161616\n    width: 15rem\n    color: white\n\n    display: flex\n    flex-direction: column\n\n    div.user\n        padding: 2rem\n        display: flex\n        justify-content: flex-start\n        align-items: center\n        img\n            width: 3rem\n            aspect-ratio: 1 / 1\n            border-radius: 1.5rem\n        p.username \n            padding: 0 1rem\n            font-size: 1.2rem\n            font-weight: 700\n\n    div.projects-top\n        width: 100%\n        padding: 1rem\n        display: grid\n        grid-template-columns: 1fr 1.5rem\n        align-items: center\n        &:hover\n            background-color: #525252\n    div.new-project\n        width: 100%\n        visibility: hidden\n        &.creating-newproject\n            animation-name: creating-project\n            animation-duration: 100ms\n            animation-timing-function: cubic-bezier(0.47, 0, 0.75, 0.72)\n            animation-delay: 0s\n            animation-iteration-count: 1\n            animation-direction: normal\n            animation-fill-mode: forwards\n            animation-play-state: running\n        input\n            // height: 1rem\n            width: 100%\n            padding: 0.7rem 2rem\n            background-color: #303030\n            outline: 0\n            border: 0\n            font-size: 1.1rem\n            color: white\n\n            animation-name: newproject\n            animation-duration: 100ms\n            animation-timing-function: cubic-bezier(0.47, 0, 0.75, 0.72)\n            animation-delay: 0s\n            animation-iteration-count: 1\n            animation-direction: normal\n            animation-fill-mode: forwards\n            animation-play-state: running\n            &::placeholder\n                font-size: 1rem\n                color: #8e8e92\n            &:focus\n                outline: 0.1rem solid #0083ff\n                outline-offset: -0.1rem\n    div.projects\n        display: flex\n        flex-direction: column\n        div.project\n            padding: 0.5rem 1rem\n            padding-left: 2rem\n            display: grid\n            grid-template-columns: 1fr 1.5rem\n            align-items: center\n            &:hover\n                background-color: #525252\n\n    div.priority\n        padding: 1rem\n        display: grid\n        grid-template-columns: 1fr 1.5rem\n        align-items: center\n        &:hover\n            background-color: #525252\n\nbutton\n    border: 0\n    height: fit-content\n    background: none\n    color: darkgrey\n    font-size: 0.8rem\n    text-align: left\n    &.small\n        width: 1.5rem\n        height: 1.5rem\n        background: url('../assets/images/delete.svg') no-repeat center\n        // visibility: hidden\n        &:hover\n            background: url('../assets/images/delete-focus.svg') no-repeat center\n        &:active\n            background: url('../assets/images/delete-active.svg') no-repeat center\n        &.add-project\n            background: url('../assets/images/addProject.svg') no-repeat center\n            &:hover\n                background: url('../assets/images/addProject-focus.svg') no-repeat center\n            &:active\n                background: url('../assets/images/addProject-active.svg') no-repeat center\n        &.edit-priority\n            background: url('../assets/images/priority.svg') no-repeat center\n            &:hover\n                background: url('../assets/images/priority-focus.svg') no-repeat center\n            &:active\n                background: url('../assets/images/addProject-active.svg') no-repeat center\n\nbutton\n    &:hover\n        cursor: pointer\n        color: white\n        font-weight: 700\n    &.setting\n        padding: 1rem\n        &:hover\n            cursor: pointer\n            background-color: #525252\n    &.all-todos\n        padding: 1rem\n        &:hover\n            cursor: pointer\n            background-color: #525252\n    &.my-lists, &.priority, &.setting, &.all-todos\n        font-size: 1.25rem\n        font-weight: 700\n        color: white\n    &.current-project\n        color: #0083ff\n        font-weight: 700","\n@keyframes opendialog\n    0%\n        opacity: 0\n        scale: 0\n        \n    100%\n        opacity: 1\n        scale: 1\n\ndialog \n    background-color: #161616\n    color: white\n    border-radius: 1rem\n    width: 35rem\n    height: 80vh\n    position: relative\n    margin: auto\n    padding: 2rem\n    border: 0\n    overflow: visible\n    box-shadow: 0.5rem 0.5rem 5rem 1rem rgba(0, 0, 0, 0.5)\n    \n    animation-name: opendialog\n    animation-duration: 500ms\n    animation-timing-function: ease-in-out\n    animation-delay: 0ms\n    animation-iteration-count: 1\n    animation-direction: normal\n    animation-fill-mode: forwards\n    animation-play-state: running\n\n    div.top-tip \n        color: #8e8e92\n        display: flex\n        justify-content: space-between\n        align-items: center\n\n        p.todo-title\n            font-size: 0.8rem\n            display: inline-flex\n            gap: 1rem\n\n        button.cancel\n            background: url('../assets/images/cancel.svg') no-repeat\n            background-size: 1.5rem\n            border: 0\n            width: 1.5rem\n            height: 1.5rem\n            border-radius: 1rem\n            &:hover\n                cursor: pointer\n                background: url('../assets/images/cancel-hover.svg') no-repeat\n                background-color: #252525\n            &:active\n                scale: 0.7\n            \n\n    input.todo-description \n        color: white\n        font-size: 1.5rem\n        margin-top: 2rem\n        border: 0\n        outline: 0\n        background: none\n        font-size: 1rem\n        padding: 0.5rem\n        border-radius: 0.5rem\n        letter-spacing: 2px\n        color: white\n        width: 100%\n        height: fit-content\n        &:hover\n            background-color: black\n\n    div.options \n        margin-top: 1rem\n        display: flex\n        gap: 1rem\n        button\n            background-color: #252525\n            border: 0\n            color: #ccc\n            font-size: 0.8rem\n            border-radius: 0.9rem\n            padding: 0.5rem 1rem\n            padding-left: 2.5rem\n            font-weight: 700\n            background-repeat: no-repeat\n            background-position-y: center\n            background-position-x: 0.7rem\n            &:hover\n                background-color: #424242\n                color: #fff\n            &:active\n                scale: 0.9\n            &.remindme \n                background-image: url('../assets/images/remindme.svg')\n                background-size: 1.5rem\n            &.change-category \n                background-image: url('../assets/images/category.svg')\n                background-size: 1.3rem\n            &.change-priority \n                background-image: url('../assets/images/priority-active.svg')\n                background-size: 1.3rem\n            &.delete \n                background-image: url('../assets/images/delete-active.svg')\n                background-size: 1.3rem\n\n    div.note \n        margin-top: 2rem\n        h3 \n            font-size: 0.8rem\n            color: #8e8e92\n            font-weight: 500\n            margin-bottom: 1rem\n\n        textarea\n            border: 0\n            outline: 0\n            background: none\n            font-size: 1rem\n            overflow: hidden\n            padding: 0.5rem\n            border-radius: 0.5rem\n            letter-spacing: 2px\n            color: white\n            width: 100%\n            height: fit-content\n            resize: vertical\n\n            &:empty::before\n                content: \"Placeholder still possible\"\n                color: gray\n            \n            &:hover\n                background-color: black\n\n    div.priorities\n        position: fixed\n        right: 5rem\n        top: 3rem\n        display: flex\n        flex-direction: column\n        width: fit-content\n        flex-direction: column\n        // background-color: #333\n        border-radius: 0.5rem\n        visibility: hidden\n        &.visible\n            visibility: visible\n            animat